import React from 'react';
import { Link , IndexLink} from 'react-router'
import { Menu, Icon} from 'antd';
const { SubMenu } = Menu;

class CusMenu extends React.Component {
  constructor(props){
    super(props);
    if(location.pathname === '/user/about/mylog'){ //-
      this.state = {
        current: location.pathname,
        openKeys: ['/user','/user/about']
      }
    }else{
      this.state = { //es6 stateconstructor
        current: '0', // key
        openKeys: [], //323 ['sub2','sub3']
      }
    }
    this.keysMap = {}; //mapopenChange
    this.MenuChildRender = [];
  }
  handleClick = (e) => { //
    this.setState({ current: e.key });
  }
  onOpenChange = (openKeys) => { //
    const state = this.state;
    const latestOpenKey = openKeys.find(key => !(state.openKeys.indexOf(key) > -1)); 
    const latestCloseKey = state.openKeys.find(key => !(openKeys.indexOf(key) > -1)); 
    let nextOpenKeys = [];
    if (latestOpenKey) {
      nextOpenKeys = this.getAncestorKeys(latestOpenKey).concat(latestOpenKey);
    }
    if (latestCloseKey) {
      nextOpenKeys = this.getAncestorKeys(latestCloseKey);
    }
    this.setState({ openKeys: nextOpenKeys });
  }
  getAncestorKeys = (key) => { //keyvalue
    return this.keysMap[key] || [];
  }
  renderMenu(){
    const repeatRender = ({path,label,icon,child},i) => { //list
      if(!child || !child.length){
        if(path === '/business/list'){// -->
          path += '/1234567'
        }
        return (<Menu.Item key={path}><Link to={path}>{icon && <Icon type={icon} />}{label}</Link></Menu.Item>)
      }
      // ifthis.keysMap
      if(icon){ //icon,this.turnoverPathkey
        this.turnoverPath = [path];
      }else{//child
        this.keysMap[path] = this.turnoverPath;
      }
      return (<SubMenu key={path} title={<span>{icon && <Icon type={icon} />}<span>{label}</span></span>}>
            {child.map(repeatRender)}
          </SubMenu>)
    }
    const {menuList} = this.props;
    this.MenuChildRender = menuList.map(repeatRender); //Menu
  }
  render() {
    if(!this.MenuChildRender.length){
      this.renderMenu();
    }
    return (
        <Menu
          mode="inline"
          openKeys={this.state.openKeys}
          selectedKeys={[this.state.current]}
          style={{ height: '100%' }}
          onOpenChange={this.onOpenChange}
          onClick={this.handleClick}
        >
          <Menu.Item key="0">
            <IndexLink to="/"><Icon type="home" /></IndexLink>
          </Menu.Item>
          {this.MenuChildRender}
        </Menu>
    );
  }
}

export default CusMenu





