let menuList = [
  {
    path:'business',
    label:'',
    icon:'bar-chart',
    child:[
      {
        path:'list',
        label:'',
        child:[]
      },
    ]
  },
  {
    path:'user',
    label:'',
    icon:'user',
    child:[
      {
        path:'about',
        label:'',
        child:[
          {
            path:'mylog',
            label:''
          }
        ]
      },
      {
        path:'seting',
        label:'',
        child:[
          {
            path:'info',
            label:''
          }
        ]
      }
    ]
  }
];

// '/',
//   user --> /user
//   userabout --> /user/about
function addFatherPath(v,i){
	if(v.icon){//
		v.path = '/' + v.path;
		if(v.child && v.child.length){
			eachChild(v);
		}
	}
}
function eachChild(v){
	v.child.forEach((vv,ii) => {
		vv.path = v.path + '/' + vv.path;
		if(vv.child && vv.child.length){
			eachChild(vv);
		}
	})
}
menuList.forEach(addFatherPath);

export default menuList




